import { adalFetch, AuthenticationContext } from 'react-adal';

export const adalConfig = {
    cacheLocation: 'localStorage',
    clientId: process.env.NODE_ENV !== 'production' ? '9fa8cac6-4668-449a-9232-f48c60a057c1' : 'c23fa0bc-275e-45a9-a518-b33893062e6b',
    endpoints: {
        api: 'https://wbengineering.sharepoint.com/'
    },
    postLogoutRedirectUri: window.location.origin,
    tenant: 'wbengineering.onmicrosoft.com'
};

export const authContext = new AuthenticationContext(adalConfig);

export const adalApiFetch = (url: string, options: any) => {
    const headers: any = {};
    const entries = options.headers.entries();
    let item = entries.next();
    while(!item.done) {
        const value = item.value;
        headers[value[0]] = value[1];
        item = entries.next();
    }
    
    const o = {
        ...options,
        headers
    };
    return adalFetch(authContext, adalConfig.endpoints.api, fetch, url, o);
}


