import * as React from 'react';
import { Col, Grid, Row } from 'react-bootstrap';

import LoggedUserInfoContainer from '../containers/Common/LoggedUserContainer';
import LogOutContainer from '../containers/Common/LogOutContainer';
import ProgressContainer from '../containers/Common/ProgressContainer';
import RootModalContainer from '../containers/Common/RootModalContainer';
import { NavMenu } from './NavMenu';

export class Layout extends React.Component<{}, {}> {
    public render() {
        return <Grid fluid={true} className="app-container">
            <Row>
                <Col sm={2}>
                    <ProgressContainer />
                    <RootModalContainer />
                </Col>
                <Col sm={10}>
                    <LoggedUserInfoContainer />
                    &nbsp;
                    <LogOutContainer />
                </Col>
            </Row>
            <Row>
                <Col sm={2}>
                    <NavMenu />
                </Col>
                <Col sm={10} className="content-container">
                    {this.props.children}
                </Col>
            </Row>
        </Grid>
    }
}


