import './styles.css';

import * as React from 'react';
import { Button, Dropdown, Panel, Well } from 'react-bootstrap';

import { IMultipleSelectProps } from './MultipleSelectProps';
import { IMultipleSelectState } from './MultipleSelectState';

export default class MultipleSelect<T> extends React.PureComponent<IMultipleSelectProps<T>, IMultipleSelectState<T>> {
    constructor(props: IMultipleSelectProps<T>) {
        super(props);

        this.selected = this.selected.bind(this);
        this.onSelected = this.onSelected.bind(this);
        this.equals = this.equals.bind(this);
        this.renderSelectedItemsArea = this.renderSelectedItemsArea.bind(this);
        this.onSelectAll = this.onSelectAll.bind(this);
        this.renderItems = this.renderItems.bind(this);
        this.renderGroupedItems = this.renderGroupedItems.bind(this);

        this.state = { selectedItems: props.selectedItems, selectAll: false }
    }

    public componentWillReceiveProps(nextProps: IMultipleSelectProps<T>) {
        this.setState({
            selectAll: false,
            selectedItems: nextProps.selectedItems
        });
    }

    public selected(selectedItem: T): boolean {
        return this.state.selectedItems
            .some(item => this.equals(item, selectedItem));
    }

    public onSelected(e: any, selectedItem: T) {
        e.stopPropagation();
        e.nativeEvent.stopImmediatePropagation();

        const selectedItems = this.state.selectedItems.slice();
        const selectdItemIndex = selectedItems
            .findIndex(item => this.equals(item, selectedItem));

        if (selectdItemIndex >= 0) {
            // already selected
            selectedItems.splice(selectdItemIndex, 1);
        } else {
            // not selected
            selectedItems.push(selectedItem);
        }
        this.setState({
            ...this.state,
            selectedItems
        });
        this.props.onSelectionChanged(selectedItems);
    }

    public equals(item1: T, item2: T) {
        return this.props.valueExtractor(item1) === this.props.valueExtractor(item2);
    }

    public renderSelectedItemsArea() {
        if (this.state.selectedItems.length > 0) {
            return <Well className="multiple-select-well">
                {
                    this.state.selectedItems
                        .map(item => this.props.titleExtractor(item)).join(', ')
                }
            </Well>
        } else {
            return <Well className="multiple-select-well">No selected items</Well>;
        }
    }

    public onSelectAll() {
        let selectAll: boolean = false;
        let selectedItems: T[] = [];

        if (!this.state.selectAll) {
            // need select all
            selectAll = true;
            selectedItems = this.props.items.slice();
        }
        this.setState({
            ...this.state,
            selectAll,
            selectedItems           
        });
        this.props.onSelectionChanged(selectedItems);
    }

    public renderItems(items: T[]) {
        return <div className="list-group">
            {
                items.map(item =>
                    <div className="list-group-item" 
                        key={this.props.valueExtractor(item)} 
                        role="button" 
                        // tslint:disable-next-line:jsx-no-lambda
                        onClick={e => this.onSelected(e, item)}>
                        <input
                            role="button"
                            type="checkbox"
                            value={this.props.valueExtractor(item)}
                            // tslint:disable-next-line:jsx-no-lambda
                            onChange={e => this.onSelected(e, item)}
                            checked={this.selected(item)} />
                        &nbsp;
                        <label role="button">
                            {this.props.titleExtractor(item)}
                        </label>
                    </div>)
            }
        </div>
    }

    public renderGroupedItems() {
        const groups = this.props.items.reduce((g: any, item: T) => {
            if (this.props.groupBy) {
                const val = this.props.groupBy(item);
                g[val] = g[val] || [];
                g[val].push(item);
            }
            return g;
        }, {});

        return Object.keys(groups).map((key: any) =>
            <Panel key={key}>
                <Panel.Heading>
                    <Panel.Title toggle={true}>
                        {key}
                    </Panel.Title>
                </Panel.Heading>
                <Panel.Collapse>
                    <Panel.Body>
                        {this.renderItems(groups[key])}
                    </Panel.Body>
                </Panel.Collapse>
            </Panel>);
    }

    public render() {
        const selectedItems = this.renderSelectedItemsArea();

        let content = null;

        if (this.props.groupBy) {
            content = this.renderGroupedItems();
        } else {
            content = this.renderItems(this.props.items);
        }

        return <div>
            <div hidden={!this.props.selectedItemsAreaVisible}>
                {selectedItems}
            </div>
            <Dropdown id={this.props.id} className="multiple-select-dropdown">
                <Dropdown.Toggle className="form-control control-label multiple-select-dropdown-title">
                    {this.props.title}
                </Dropdown.Toggle>
                <Dropdown.Menu className="super-colors">
                    <div hidden={!this.props.selectAllButtonVisible}>
                        <div className="list-group-item">
                            <Button bsStyle="primary"
                                onClick={this.onSelectAll}
                                disabled={this.props.items.length === 0}
                                active={this.state.selectAll}>
                                Select/Unselect All
                        </Button>
                        </div>
                    </div>
                    {content}
                </Dropdown.Menu>
            </Dropdown>
        </div>
    }
}


