import * as React from 'react';
import { Link, NavLink } from 'react-router-dom';

export class NavMenu extends React.Component<{}, {}> {
    public render() {
        return <div className='main-nav'>
                <div className='navbar navbar-inverse'>
                <div className='navbar-header'>
                    <button type='button' className='navbar-toggle' data-toggle='collapse' data-target='.navbar-collapse'>
                        <span className='sr-only'>Toggle navigation</span>
                        <span className='icon-bar'/>
                        <span className='icon-bar'/>
                        <span className='icon-bar'/>
                    </button>
                    <Link className='navbar-brand' to={ '/' }>WB.Dashboards</Link>
                </div>
                <div className='clearfix'/>
                <div className='navbar-collapse collapse'>
                    <ul className='nav navbar-nav'>
                        <li>
                            <NavLink exact={true} to={ '/' } activeClassName='active'>
                                <span className='glyphicon glyphicon-tasks'/> Projects Dashboard
                            </NavLink>
                        </li>
                        <li>
                            <NavLink to={ '/team' } activeClassName='active'>
                                <span className='glyphicon glyphicon-user'/> Team Dashboard
                            </NavLink>
                        </li>
                        <li>
                            <NavLink to={ '/resources' } activeClassName='active'>
                                <span className='glyphicon glyphicon-signal'/> Resource Planning
                            </NavLink>
                        </li>
                    </ul>
                </div>
            </div>
        </div>;
    }
}



